<template>
  <div class="echartsDemo">
    <div class="header">
      <el-button class="header-btn" @click="goBack">返回</el-button>
      <h1>设备统计可视化</h1>
      <div class="showTime">{{nowDate}}</div>
    </div>
    <div class="wrap">
      <div class="mainbox">
        <div class="columnLeft">
          <div class="panel barLeft1">
            <h2>密钥使用量</h2>
            <div class="chart"></div>
            <div class="panel-footer"></div>
          </div>
        </div>
        <div class="columnCenter">
          <div class="no">
            <div class="no-hd">
              <ul>
                <li>{{deviceNum}}</li>
                <li>{{onlineNum}}</li>
              </ul>
            </div>
            <div class="no-bd">
              <ul>
                <li>接入设备数量</li>
                <li>在线数量</li>
              </ul>
            </div>
          </div>
          <div class="no-gauge">
            <div class="chart"></div>
            <p class="title">密钥使用总量</p>
          </div>
        </div>
        <div class="columnRight">
          <div class="panel barRight1">
            <h2>加密、解密数量</h2>
            <div class="chart"></div>
            <div class="panel-footer"></div>
          </div>
        </div>
      </div>
      <div class="footer">
        <div class="bottomWrap">
          <p><span class="hrSpan"></span><span style="padding: 0 10px">最近业务信息</span><span class="hrSpan"></span></p>
          <el-table :data="userList" stripe>
            <el-table-column type="index" label="序号"></el-table-column>
            <el-table-column label="操作者" prop="operator" :show-overflow-tooltip="true">
              <template slot-scope="scope">
                <span>{{scope.row.operator == null || scope.row.operator == '' ? '-' : scope.row.operator}}</span>
              </template>
            </el-table-column>
            <el-table-column label="IP地址" prop="operateIp" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column label="操作模块" prop="operateModel"></el-table-column>
            <el-table-column label="耗时(毫秒)" prop="execTime"></el-table-column>
            <el-table-column label="操作结果" prop="operateStatus">
              <template slot-scope="scope">
                <span v-if="scope.row.operateStatus === 0">成功</span>
                <span v-else-if="scope.row.operateStatus === 1">失败</span>
              </template>
            </el-table-column>
            <el-table-column label="详情" prop="detail">
              <template slot-scope="scope">
                <span>{{scope.row.detail == null || scope.row.detail == '' ? '-' : scope.row.detail}}</span>
              </template>
            </el-table-column>
            <el-table-column label="更新时间" prop="updateTime" :show-overflow-tooltip="true">
              <template slot-scope="scope">
                <span>{{scope.row.updateTime == null || scope.row.updateTime == '' ? '-' : scope.row.updateTime}}</span>
              </template>
            </el-table-column>
          </el-table>
          <div class="bottom-footer"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import * as echarts from 'echarts';

  export default {
    data() {
      return {
        deviceNum: '',
        onlineNum: '',
        keyUseNum: '',
        totalInfo: {},
        nowDate: null,
        timer: null,//定时器名称
        total: 0,
        userList: [],
        queryInfo: {
          pagenum: 1,
          pagesize: 4
        },
      }
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      }else{
        return next('/404')
      }
    },
    created() {
      this.currentTime();
      this.getTotalInfo();
      this.jishi();
    },
    methods: {
      goBack() {
        // this.$router.push('/pwsp')
        this.$router.go(-1)
      },
      async getTotalInfo() {
        const {data: res} = await this.$http.get(`status/getDeviceStatusInfo?ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error('获取数据信息失败！');
        this.totalInfo = res.data;
        this.deviceNum = res.data.deviceStatusInfo.deviceNum;
        this.onlineNum = res.data.deviceStatusInfo.onlineNum;
        this.keyUseNum = res.data.deviceStatusInfo.keyUseNum;
        this.userList = res.data.logs;
        this.barLeft1(this.totalInfo);
        this.barRight1(this.totalInfo);
        this.gaugeLine(this.keyUseNum);
      },
      jishi(){
        this.timer = setInterval(() => {
          this.getTotalInfo()
        }, 5000)
      },
      currentTime() {
        setInterval(this.formatDate, 1000);
      },
      formatDate() {
        let date = new Date();
        let year = date.getFullYear(); // 年
        let month = date.getMonth() + 1; // 月
        let day = date.getDate(); // 日
        let week = date.getDay(); // 星期
        let weekArr = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"];
        let hour = date.getHours(); // 时
        hour = hour < 10 ? "0" + hour : hour;
        let minute = date.getMinutes(); // 分
        minute = minute < 10 ? "0" + minute : minute;
        let second = date.getSeconds(); // 秒
        second = second < 10 ? "0" + second : second;
        this.nowDate = `${year}年${month}月${day}日 ${hour}:${minute}:${second}`;
      },
      barLeft1(num) {
        let myChart = echarts.getInstanceByDom(document.querySelector('.barLeft1 .chart'));
        if(myChart == null){
          myChart = echarts.init(document.querySelector('.barLeft1 .chart'));
        }
        const xData = num.deviceStatusInfo.everyDayKeyNum;
        let newArryX = xData.map(item => {
          return item.timeInfo
        });
        let newArryY = xData.map(item => {
          return item.keyNum
        });
        myChart.setOption({
          color: ['#2f89cf'],
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "shadow"
            }
          },
          grid: {
            left: "0%",
            top: "10px",
            right: "0%",
            bottom: "4%",
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: newArryX,
            axisTick: {
              alignWithLabel: true
            },
            axisLabel: {
              color: 'rgba(255,255,255,.6)',
              fontSize: '12',
              // rotate:28//切斜度
            },
            axisLine: {
              show: false
            }
          },
          yAxis: {
            type: 'value',
            minInterval:1,
            axisLabel: {
              color: "rgba(255,255,255,.6) ",
              fontSize: 12,
            },
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,.1)",
                width: 2
              }
            },
            // y轴分割线的颜色
            splitLine: {
              lineStyle: {
                color: "rgba(255,255,255,.1)"
              }
            },
          },
          series: [{
            name: '合计',
            type: 'bar',
            barWidth: '20%',
            data: newArryY,
            itemStyle: {
              borderRadius: 5
            }
          }]
        });
        window.addEventListener("resize", function () {
          myChart.resize();
        });
      },
      barRight1(num) {
        let myChart = echarts.getInstanceByDom(document.querySelector('.barRight1 .chart'));
        if(myChart == null){
          myChart = echarts.init(document.querySelector('.barRight1 .chart'));
        }
        myChart.setOption({
          color: ['#2f89cf'],
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "shadow"
            }
          },
          grid: {
            left: "0%",
            top: "10px",
            right: "0%",
            bottom: "4%",
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: ['加密数量', '解密数量','加解密数据总量'],
            axisTick: {
              alignWithLabel: true
            },
            axisLabel: {
              color: 'rgba(255,255,255,.6)',
              fontSize: '12'
            },
            axisLine: {
              show: false
            }
          },
          yAxis: {
            type: 'value',
            axisLabel: {
              color: "rgba(255,255,255,.6) ",
              fontSize: 12,
              formatter: function (value, index) {
                if (value >= 10000 && value < 10000000) {
                  value = value / 10000 + "万";
                } else if (value >= 10000000) {
                  value = value / 10000000 + "千万";
                }
                return value;
              },
            },
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,.1)",
                width: 2
              }
            },
            // y轴分割线的颜色
            splitLine: {
              lineStyle: {
                color: "rgba(255,255,255,.1)"
              }
            },

          },
          series: [{
            name: '合计',
            type: 'bar',
            barWidth: '15%',
            data: [num.deviceStatusInfo.encDataNum, num.deviceStatusInfo.decDataNum,num.deviceStatusInfo.encDataNum + num.deviceStatusInfo.decDataNum],
            itemStyle: {
              borderRadius: 5
            }
          }]
        });
        window.addEventListener("resize", function () {
          myChart.resize();
        });
      },
      gaugeLine(num) {
        const numStr = num + '';
        const strLength = numStr.length;
        let firstNum = numStr.slice(0, 1);
        let maxNum = (Number(firstNum) + 1) + Array(strLength).join(0);
        if (maxNum < 100) {
          maxNum = 100;
        } else if (maxNum < 1000 && maxNum >= 100) {
          maxNum = 1000;
        }else if (maxNum < 10000 && maxNum >= 1000) {
          maxNum = 10000;
        }else if (maxNum < 100000 && maxNum >= 10000) {
          maxNum = 100000;
        }
        let myChart = echarts.getInstanceByDom(document.querySelector('.no-gauge .chart'));
        if(myChart == null){
          myChart = echarts.init(document.querySelector('.no-gauge .chart'));
        }
        myChart.setOption({
          series: [{
            type: 'gauge',
            center: ['50%', '65%'],
            min: 0,
            max: maxNum,
            startAngle: 200,
            endAngle: -20,
            itemStyle: {
              color: '#58D9F9',
            },
            progress: {
              show: true,
              width: 15
            },
            axisLine: {
              lineStyle: {
                width: 15
              },

            },
            axisTick: {//刻度样式
              distance: -30,
              splitNumber: 5,//分隔线之间分割的刻度数
              lineStyle: {
                width: 2,
                color: '#999'
              },

            },
            splitLine: {//分隔线样式
              distance: -46,//分隔线与轴线的距离
              length: 14,
              lineStyle: {
                width: 3,
                color: '#999'
              }
            },
            axisLabel: {
              distance: -25,
              color: 'white',
              formatter: function (value, index) {
                if (value >= 10000 && value < 10000000) {
                  value = value / 10000 + "万";
                } else if (value >= 10000000) {
                  value = value / 10000000 + "千万";
                }
                return value;
              },
            },
            detail: {//仪表盘详情，用于显示数据
              valueAnimation: true,//是否开启标签的数字动画。
              fontSize: 25,
              fontWeight: 'bolder',
              formatter: function (value, index) {
                if (value >= 10000 && value < 10000000) {
                  value = value / 10000 + "万";
                } else if (value >= 10000000) {
                  value = value / 10000000 + "千万";
                }
                return value;
              },
              color: '#ffeb7b',
              offsetCenter: [0, '80%'],
              fontFamily: 'electronicFont',
            },
            data: [{
              value: num,
            }],
          }]
        });
        window.addEventListener("resize", function () {
          myChart.resize();
        });
      },
    },
    beforeDestroy() {  // 销毁定时器
      clearInterval(this.timer);
      if (this.formatDate) {
        clearInterval(this.formatDate);
      }
    }
  }
</script>
<style lang="less">
  .echartsDemo {
    position: relative;
    overflow: hidden;
    height: 100%;
    min-width: 1200px;
    min-height: 910px;
    background: url(../assets/image/bg.jpg) no-repeat;
    background-position: center center;
    background-attachment: inherit;
    background-size: cover;
    .header {
      position: relative;
      height: 100px;
      background: url(../assets/image/head_bg.png) no-repeat;
      background-size: 100% 100%;
      .header-btn {
        position: absolute;
        left: 30px;
        top: 12px;
      }
      h1 {
        line-height: 80px;
        font-size: 38px;
        color: #fff;
        text-align: center;
      }
      .showTime {
        position: absolute;
        right: 30px;
        top: 0;
        line-height: 75px;
        color: rgba(255, 255, 255, 0.7);
        font-size: 20px;
      }
    }
    .wrap {
      width: 100%;
      height: calc(100% - 120px);
      position: relative;
      .mainbox {
        position: relative;
        height: 310px;
        padding: 10px 10px 0;
        margin: 10px 15px;
        .columnLeft {
          position: absolute;
          left: 0;
          top: 0;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          width: 30%;
          height: 100%;
        }
        .columnCenter {
          position: absolute;
          top: 0;
          left: 30%;
          padding: 0 20px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          width: 40%;
          height: 310px;
        }
        .columnRight {
          position: absolute;
          right: 0;
          top: 0;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          width: 30%;
          height: 100%;
        }
        .panel {
          position: relative;
          height: 310px;
          padding: 0 15px 40px;
          margin-bottom: 15px;
          border: 1px solid rgba(25, 186, 139, 0.17);
          &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 10px;
            height: 10px;
            border-left: 2px solid #02a6b5;
            border-top: 2px solid #02a6b5;
            content: '';
          }
          &::after {
            position: absolute;
            top: 0;
            right: 0;
            width: 10px;
            height: 10px;
            border-right: 2px solid #02a6b5;
            border-top: 2px solid #02a6b5;
            content: '';
          }
          h2 {
            height: 48px;
            line-height: 48px;
            color: #fff;
            text-align: center;
            letter-spacing: 2px;
            font-size: 18px;
            font-weight: 400;
            a {
              margin: 0 10px;
              color: #fff;
              text-decoration: none;
            }
          }
          .chart {
            height: 240px;
          }
          .panel-footer {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            &::before {
              position: absolute;
              left: 0;
              bottom: 0;
              width: 10px;
              height: 10px;
              border-left: 2px solid #02a6b5;
              border-bottom: 2px solid #02a6b5;
              content: '';
            }
            &::after {
              position: absolute;
              bottom: 0;
              right: 0;
              width: 10px;
              height: 10px;
              border-right: 2px solid #02a6b5;
              border-bottom: 2px solid #02a6b5;
              content: '';
            }
          }
        }
        // no数字模块
        .no {
          background: rgba(101, 132, 226, 0.1);
          padding: 10px 15px;
          margin-bottom: 10px;
          .no-hd {
            position: relative;
            border: 1px solid rgba(25, 186, 139, 0.17);
            &::before {
              position: absolute;
              top: 0;
              left: 0;
              content: '';
              width: 30px;
              height: 10px;
              border-top: 2px solid #02a6b5;
              border-left: 2px solid #02a6b5;
            }
            &::after {
              position: absolute;
              bottom: 0;
              right: 0;
              content: '';
              width: 30px;
              height: 10px;
              border-right: 2px solid #02a6b5;
              border-bottom: 2px solid #02a6b5;
            }
            ul {
              display: flex;
              li {
                position: relative;
                flex: 1;
                line-height: 80px;
                font-size: 70px;
                color: #ffeb7b;
                text-align: center;
                font-family: 'electronicFont';

              }
              li:nth-of-type(1){
                &::after {
                  content: '';
                  position: absolute;
                  top: 25%;
                  right: 0;
                  height: 50%;
                  width: 1px;
                  background: rgba(255, 255, 255, 0.2);
                }
              }
            }
          }
          .no-bd {
            ul {
              display: flex;
              li {
                flex: 1;
                text-align: center;
                color: rgba(255, 255, 255, 0.7);
                font-size: 18px;
                height: 40px;
                line-height: 40px;
                padding-top: 10px;
              }
            }
          }
        }
        .no-gauge {
          text-align: center;
          color: white;
          font-size: 20px;
          .chart {
            height: 220px;
          }
          .title {
            font-size: 22px;
            color: white;
            padding: 5px 0;
          }
        }
      }
      .footer {
        position: absolute;
        bottom: 0;
        width: 100%;
        .bottomWrap{
          position: relative;
          padding: 10px 60px;
          margin: 0 15px;
          &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 10px;
            height: 10px;
            border-left: 2px solid #02a6b5;
            border-top: 2px solid #02a6b5;
            content: '';
          }
          &::after {
            position: absolute;
            top: 0;
            right: 0;
            width: 10px;
            height: 10px;
            border-right: 2px solid #02a6b5;
            border-top: 2px solid #02a6b5;
            content: '';
          }
          p {
            text-align: center;
            color: white;
            letter-spacing: 2px;
            font-size: 20px;
          }
          .hrSpan {
            display: inline-block;
            position: relative;
            top: -6px;
            width: 300px;
            border: 0.5px solid #5b86dde8;
          }
          .bottom-footer{
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            &::before {
              position: absolute;
              left: 0;
              bottom: 0;
              width: 10px;
              height: 10px;
              border-left: 2px solid #02a6b5;
              border-bottom: 2px solid #02a6b5;
              content: '';
            }
            &::after {
              position: absolute;
              bottom: 0;
              right: 0;
              width: 10px;
              height: 10px;
              border-right: 2px solid #02a6b5;
              border-bottom: 2px solid #02a6b5;
              content: '';
            }
          }
        }
      }
      .el-table {
        color: white;
      }
      .el-table,
      .el-table__expanded-cell {
        background-color: transparent;
      }
      .el-table th,
      .el-table tr {
        background-color: transparent;
      }

      .el-pagination button:disabled {
        background: transparent;
      }
      .el-pager li {
        background-color: transparent;
      }
      .el-pagination .btn-next, .el-pagination .btn-prev {
        background-color: transparent;
      }
      .el-table--striped .el-table__body tr.el-table__row--striped td {
        background: #4152ac61;
      }
    }
  }
</style>
